.f-radio {
  display: inline-block; }

.f-radio__inner {
  display: flex;
  flex-wrap: nowrap;
  align-items: center; }

.f-radio__field {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute; }

.f-radio__radio {
  display: flex;
  align-items: center;
  position: relative; }

.f-radio__field:not(:disabled) + .f-radio__radio {
  cursor: pointer; }

.f-radio__field:disabled + .f-radio__radio {
  cursor: not-allowed; }

.f-radio__radio:before, .f-radio__radio:after {
  content: '';
  display: block;
  font-size: 1.2em; }

.f-radio__radio:before {
  color: silver;
  border: 2px solid currentColor;
  border-radius: 50%;
  width: 1em;
  height: 1em;
  display: block;
  transition: color 0.2s cubic-bezier(0.51, 0.92, 0.24, 1);
  will-change: color;
  flex-shrink: 0; }

.f-radio__field:disabled + .f-radio__radio:before {
  color: silver; }

.f-radio__radio:after {
  transform: scale(0);
  background-color: currentColor;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  transition: transform 0.2s cubic-bezier(0.51, 0.92, 0.24, 1);
  will-change: color;
  position: absolute;
  left: 0;
  top: 0; }

.f-radio__field:checked + .f-radio__radio:after {
  transform: scale(0.3);
  visibility: visible;
  will-change: transform; }

.f-radio__label {
  user-select: none;
  font-size: 1em; }
  .f-radio__label {
    color: inherit;
    position: relative;
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
    transition: color 0.2s cubic-bezier(0.51, 0.92, 0.24, 1);
    will-change: color;
    padding-left: 1em; }
  .f-radio__field:not(:disabled) + .f-radio__radio + .f-radio__label {
    cursor: pointer; }
  .f-radio__field:disabled + .f-radio__radio + .f-radio__label {
    cursor: not-allowed; }
  .f-radio__label .f-radio__label-text {
    color: inherit; }
    .f-radio__field:hover + .f-radio__radio + .f-radio__label .f-radio__label-text {
      color: inherit; }
    .f-radio__field:checked + .f-radio__radio + .f-radio__label .f-radio__label-text {
      color: inherit; }

.f-radio__label-text {
  color: gray;
  font-size: 1em;
  line-height: 1; }
  .f-radio__field[disabled].f-radio__field[disabled] + .f-radio__label .f-radio__label-text {
    color: silver; }

.f-radio__indicator {
  pointer-events: none;
  user-select: none;
  color: silver; }
  .f-radio__indicator--optional {
    font-size: 0.5em; }
  .f-radio__indicator--required {
    font-size: 1em; }
  .has-error .f-radio__field:hover ~ .f-radio__indicator,
  .has-error .f-radio__field:focus ~ .f-radio__indicator {
    color: #d33944 !important; }
  .f-radio__field[disabled].f-radio__field[disabled] ~ .f-radio__indicator {
    color: silver; }

.f-radio__message {
  font-size: 0.8em;
  margin-left: 0; }
  .f-radio__message--instruction {
    color: gray; }
  .f-radio__message--error {
    color: #d33944; }
    .has-error .f-radio__message--error::-moz-selection {
      color: white;
      background: #d33944 !important;
      text-shadow: none; }
    .has-error .f-radio__message--error::selection {
      color: white !important;
      background: #d33944 !important;
      text-shadow: none; }
  .f-radio__message--error + .f-radio__message--instruction {
    display: none; }
